<template>
  <div class="container">
    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
      <h3>Editar nota</h3>
      <input
        v-model="notaEditar.nombre"
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
      />
      <input
        v-model="notaEditar.descripcion"
        type="text"
        class="form-control my-2"
        placeholder="Descripción"
      />
      <b-button type="submit" class="btn-warning my-2">Editar</b-button>
      <b-button type="submit" @click="editar = false" class="my-2"
        >Cancelar</b-button
      >
    </form>

    <form @submit.prevent="agregarNota()" v-if="!editar">
      <h3>Agregar nueva nota</h3>
      <input
        v-model="nota.nombre"
        type="text"
        class="form-control my-2"
        placeholder="Nombre"
      />
      <input
        v-model="nota.descripcion"
        type="text"
        class="form-control my-2"
        placeholder="Descripción"
      />
      <b-button type="submit" class="btn-success my-2 btn-block"
        >Agregar</b-button
      >
    </form>
    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nombre</th>
          <th scope="col">Descripción</th>
          <th scope="col">Fecha</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th scope="row">{{ item._id }}</th>
          <td>{{ item.nombre }}</td>
          <td>{{ item.descripcion }}</td>
          <td>{{ item.date }}</td>
          <td>
            <b-button
              class="btn-warning btn-sm mx-2"
              @click="activarEdicion(item._id)"
              >Actualizar</b-button
            >
            <b-button
              class="btn-danger btn-sm mx-2"
              @click="eliminarNota(item._id)"
              >Eliminar</b-button
            >
          </td>
        </tr>
      </tbody>
    </table>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.text }}
    </b-alert>
  </div>
</template>
<script>
export default {
  data() {
    return {
      notas: [],
      mensaje: { color: "success", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      nota: { nombre: "", descripcion: "" },
      notaEditar: {},
      editar: false,
    };
  },
  created() {
    this.listarNotas();
  },
  methods: {
    listarNotas() {
      this.axios
        .get("notas")
        .then((response) => {
          this.notas = response.data;
        })
        .catch((e) => {
          console.log("error" + e);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
    alert(message) {
      this.mensaje.color = "success";
      this.mensaje.text = message;
      this.showAlert();
    },
    agregarNota() {
      this.axios
        .post("nueva-nota", this.nota)
        .then((res) => {
          this.notas.unshift(res.data);
          this.mensaje.color = "success";
          this.mensaje.text = "Nota agregada!";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response.data.error.errors.nombre.message);
          this.mensaje.color = "danger";
          this.mensaje.text = e.response.data.error.errors.nombre.message;
          this.showAlert();
        });
      this.nota = {};
    },
    eliminarNota(id) {
      console.log(id);
      this.axios
        .delete(`/nota/${id}`)
        .then((res) => {
          const index = this.notas.findIndex(
            (item) => item._id === res.data._id
          );
          this.notas.splice(index, 1);
          this.mensaje.color = "danger";
          this.mensaje.text = "Nota eliminada!";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
    activarEdicion(id) {
      this.editar = true;
      this.editarNota(id);
      this.axios
        .get(`/nota/${id}`)
        .then((res) => {
          this.notaEditar = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
    editarNota(item) {
      this.axios
        .put(`/nota/${item.id}`, item)
        .then((res) => {
          const index = this.notas.findIndex((n) => n._id === res.data._id);
          this.notas[index].nombre = res.data.nombre;
          this.notas[index].descripcion = res.data.descripcion;
          this.mensaje.color = "success";
          this.mensaje.text = "Nota Editada";
          this.showAlert();
          this.editar = false;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
  },
};
</script>
